body, html {
  height: 100%;
}
#root{
  height: 100vh;
}
.App {
  text-align: center;
  height: 100%;
}

.App>div,.App>div>div{
  height: 100%;
}
.App>.row>.col-md-10{
  overflow-y: scroll;
}

.App>div>div:nth-child(1){
  background-color: #051E34;
  padding: 0;
}
.btn-wrapper{
  position: relative;
  margin-bottom: 5px;
  width: 100%;
  border: none !important;
  text-align: left;
  font-size: 14px;
  font-weight: bold;
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
  border-radius: 0 !important;
  background-color: transparent;
}

.selectedd{
  background-color: #083358;
  color: #1685e7;
}

.btn-wrapper:hover{
  background-color: #083358 !important;
  color: #1685e7;
}
.btn-wrapper:active{
  background-color: transparent !important;
}

.csv-input{
  width: 100%;
  opacity: 0;
  cursor: pointer;
}

.file-upload-btn{
  position: absolute;
  cursor: pointer;
  text-align: center;
  width: 100%;
  top: 0;
  left: 0;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.visualizer{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner-container{
  height: 100%;
  background: #1919194d;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cog_spinner {
  font-size: 200px !important;
  color: #111;
}

.main_content {
 padding-left: 0;
 padding-right: 0;
}

.branding {
  color: #fff;
  font-size: 18px;
  border-bottom: 1px solid #294865;
  margin: 20px 0px 40px -25px;
  padding: 0 0 22px 0;
}

.branding .fa {
  margin-right: 10px;
}

.features_navbar {
  min-height: 65px;
  background: #111 !important;
}

.features_navbar.navbar-light .navbar-brand {
  color: #fff;
}

.box_plot_container {
  padding-top: 40px;
}


.scatter-visualizer{
  padding: 25px;
  overflow-x: scroll;
}

.bar-a{
            fill: steelblue;
        }

.bar-b{
            fill: red;
        }
.bar-c{
            fill: yellow;
        }
.bar-d{
            fill: green;
        }
.selected-bar{
  fill: #051E34;
}

.selected-text{
  color: #051E34;
  font-size: 14px!important;
  font-weight: bold;
}

.data-text{
  font-size: 8px;
}

.svg-css{
  box-shadow:0px 0px 3px 0px grey;
  padding: 15px;
}

#checkbox-form{
  text-align: left;
}

.selectBox-wrapper{
  display: flex;
  align-items: center;
  justify-content: center;
}

.selectBox-wrapper>form{
  width: 50%;
  border: 1px solid #c7c7c7;
  padding: 8px;
  border-radius: 8px;
}

.checkbox-class{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.checkbox-class>div:nth-child(2), .ethnic-label-legend>div>div{
  height: 20px;
  width: 20px;

}

.ethnic-label-legend{
  position: absolute;
  z-index: 1;
  right:40px;
  top:25px;
  padding: 8px;
  /* width: 110px; */
  border: 1px solid #b1b1b1;
  border-radius: 8px;
}
.ethnic-label-legend>div{
  display: inline-flex;
  justify-content: space-between;
  margin-right: 8px;

}
.ethnic-label-legend>div>p{
  margin-bottom: 0px;
}

.c{
  background-color: #F24443;
}

.m{
  background-color: #11ED2E;
}

.h{
  background-color: #0F10EA;
}

.t{
  background-color: #DA42E5;
}

.n{
  background-color: #000000;
}

.custom_audio_player {
  background: #083358;
  padding: 1px 20px 10px 31px;
  /* border-radius: 10px; */
  margin-top: 20px;
  color: #fff;
}

.custom_audio_player .controls {
  margin-top: 20px;
}

.custom_audio_player .controls input {
  outline: none !important;
  border: none !important;
  box-shadow: none !important;
}

.custom_audio_player .controls .title {
  text-align: left;
  font-size: 20px;
  margin-bottom: 10px;
  margin-left: 5px;
}

.custom_audio_player .controls .fa {
  font-size: 40px;
}

.custom_audio_player .ytb {
  position: fixed;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  z-index: -10;
}

.loading{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 80px;
}
